<template>
  <div class="category_add">
    <breadcrumb path="æ–°å»ºåˆ†ç±»"></breadcrumb>
    <el-form :model="form" :rules="rules">
      <el-form-item label="åˆ†ç±»åç§°" prop="newCateName">
        <el-input v-model="form.newCateName" placeholder="è¯·è¾“å…¥æ–°çš„åˆ†ç±»åç§°"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="onSubmit">æäº¤</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script type="es6">
  import breadcrumb from '../../components/breadcrumb.vue'
  export default {
    data() {
      return {
        form: {
          newCateName: '',
        },
        rules: {
          newCateName: [
            {required: true, message: 'è¯·è¾“å…¥æ–°çš„åˆ†ç±»åç§°', trigger: 'blur'},
          ],
        },
      }
    },
    methods: {
      onSubmit() {
        if (this.form.newCateName == '') {
          this.$message({
            message: 'åˆ†ç±»åä¸èƒ½ä¸ºç©º',
            type: 'warning'
          });
          return;
        }
        this.$http.post('/admin/category/add', {
          name: this.form.newCateName,
        }).then(response => {
          if (response.data.code != 0) {
            this.$message({
              message: response.data.message,
              type: 'warning'
            });
          } else {
            this.$message({
              message: response.data.message,
              type: 'success'
            });
          }
          this.form.newCateName = '';
        }, response => {
          console.log('error:' + response);
        })
      },
    },
    components: {
      breadcrumb,
    }
  }

</script>

