<template>
  <div class="category_edit">
    <breadcrumb path="åˆ†ç±»ç¼–è¾‘"></breadcrumb>
    <h3>ç¼–è¾‘ -- {{ category.name }}</h3>
    <el-form :model="form" :rules="rules">
      <el-form-item label="åˆ†ç±»åç§°" prop="newCateName">
        <el-input v-model="form.newCateName" placeholder="è¯·è¾“å…¥æ–°çš„åˆ†ç±»åç§°"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="onSubmit">æäº¤</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script type="es6">
  import breadcrumb from '../../components/breadcrumb.vue'
  export default {
    data() {
      return {
        form: {
          newCateName: '',
        },
        rules: {
          newCateName: [
            {required: true, message: 'è¯·è¾“å…¥æ–°çš„åˆ†ç±»åç§°', trigger: 'blur'},
          ],
        },
        category: {},
      }
    },
    created() {
      this.getData();
    },
    methods: {
      getData() {
        console.log(this.$route.query)
        this.$http.get('/admin/category/edit?id=' + this.$route.query['id']).then(response => {
          this.category = response.data.category;
        }, response => {
          console.log('error:' + response);
        })
      },
      onSubmit() {
        if (this.form.newCateName == '') {
          this.$message({
            message: 'åˆ†ç±»åä¸èƒ½ä¸ºç©º',
            type: 'warning'
          });
          return;
        }
        this.$http.post('/admin/category/edit', {
          id: this.category._id.toString(),
          name: this.form.newCateName,
        }).then(response => {
          if (response.data.code != 0) {
            this.$message({
              message: response.data.message,
              type: 'warning'
            });
          } else {
            this.$message({
              message: response.data.message,
              type: 'success'
            });
          }
          this.form.newCateName = '';
          this.getData();
        }, response => {
          console.log('error:' + response);
        })
      },
    },
    components: {
      breadcrumb,
    }
  }

</script>

<style lang="stylus">
</style>



